"use client"

import { useAuth } from "@/lib/auth-context"
import { Card } from "@/components/ui/card"

export default function HomePage() {
  const { user } = useAuth()

  if (!user) {
    return null
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-blue-600">Bienvenido, {user.name}</h1>
        <p className="text-gray-600 mt-2">
          {user.role} - {user.branch}
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {user.role === "Cajero" && (
          <Card className="p-6 bg-white shadow-lg hover:shadow-xl transition-shadow border-l-4 border-blue-500">
            <h2 className="text-xl font-semibold mb-2">Módulo de Ventas</h2>
            <p className="text-gray-600 mb-4">
              Registra nuevas ventas y gestiona las transacciones diarias.
            </p>
            <a
              href="/ventas"
              className="inline-block px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
            >
              Ir a Ventas
            </a>
          </Card>
        )}

        {(user.role === "Gerente" || user.role === "Superusuario") && (
          <>
            <Card className="p-6 bg-white shadow-lg hover:shadow-xl transition-shadow border-l-4 border-blue-500">
              <h2 className="text-xl font-semibold mb-2">Reportes</h2>
              <p className="text-gray-600 mb-4">
                Visualiza estadísticas y análisis de ventas.
              </p>
              <a
                href="/reportes"
                className="inline-block px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
              >
                Ver Reportes
              </a>
            </Card>

            <Card className="p-6 bg-white shadow-lg hover:shadow-xl transition-shadow border-l-4 border-blue-500">
              <h2 className="text-xl font-semibold mb-2">Inventario</h2>
              <p className="text-gray-600 mb-4">
                Gestiona productos, marcas y existencias.
              </p>
              <a
                href="/productos"
                className="inline-block px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition-colors"
              >
                Gestionar Inventario
              </a>
            </Card>
          </>
        )}

        {user.role === "Superusuario" && (
          <Card className="p-6 bg-white shadow-lg hover:shadow-xl transition-shadow border-l-4 border-red-500">
            <h2 className="text-xl font-semibold mb-2">Empleados</h2>
            <p className="text-gray-600 mb-4">
              Administra el personal y sus roles.
            </p>
            <a
              href="/empleados"
              className="inline-block px-4 py-2 bg-red-500 text-white rounded hover:bg-red-600 transition-colors"
            >
              Gestionar Empleados
            </a>
          </Card>
        )}
      </div>

      <div className="mt-8">
        <h2 className="text-2xl font-semibold text-blue-600 mb-4">Accesos Rápidos</h2>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          {user.role !== "Cajero" && (
            <>
              <a
                href="/sucursales"
                className="p-4 bg-white rounded-lg shadow hover:shadow-md transition-shadow text-center hover:bg-blue-50"
              >
                Sucursales
              </a>
              <a
                href="/gastos"
                className="p-4 bg-white rounded-lg shadow hover:shadow-md transition-shadow text-center hover:bg-blue-50"
              >
                Gastos
              </a>
            </>
          )}
          <a
            href="/ventas"
            className="p-4 bg-white rounded-lg shadow hover:shadow-md transition-shadow text-center hover:bg-blue-50"
          >
            Nueva Venta
          </a>
          {user.role !== "Cajero" && (
            <a
              href="/marcas"
              className="p-4 bg-white rounded-lg shadow hover:shadow-md transition-shadow text-center hover:bg-blue-50"
            >
              Marcas
            </a>
          )}
        </div>
      </div>
    </div>
  )
}
